@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
}

html {
  font-family:'Work Sans', sans-serif;
  background-repeat: no-repeat;
  background-image: url(https://i.pinimg.com/originals/49/38/6a/49386a2152e55bac790bf88982e1f804.jpg);
  background-size: cover;
  height: 100vh;
}

.header {
  background-color: #d59203;
  height: 100px;
  margin-bottom: 2rem;
}

a {
  color: white;
  text-decoration: none;
}

.header__navbar {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.nav-list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.nav-list__links:hover {
  text-decoration: underline;
}

.nav-list__links {
  list-style-type: none;
}

.nav-list__links--right {
  margin-left: 2rem;
}

@media all and (min-width: 840px) {
  .header {
    font-size: 1.5rem;
    margin-bottom: 3rem;
  }
}

.main-container--about-page {
    text-align: center;
}

.header__about-paragraph {
    margin-bottom: 2rem;
    font-style: italic;
}

.info-box {
    background-color: white;
    margin: 0 1rem;
    padding: 1rem;
    border-radius: 1rem;
}

td, th {
    text-align: left;
    padding: 2px;
    font-size: 0.8rem;
  }

  .th--bold {
    font-style: bold;
    font-size: 1rem;
    color: #348872;
}

@media all and (min-width:840px) {
    .header__about {
        font-size: 3rem;
    }
    .info-box {
        max-width: 1000px;
        margin: auto;
    }

    .info-table {
        padding: 1.5rem 0.8rem;
    }

    td, th {
        padding: 8px;
        font-size: 1.5rem;
      }

      .th--bold { 
          font-size: 1.6rem;
      }

 }

.btn-get-activity {
  background-color: #8e8cc2;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  border: none;
  color: white;
  width: 50vw;
  max-width: 220px;
  font-size: 1.1rem;
  font-family: 'Work Sans', sans-serif;
}

.btn-get-activity:hover {
  cursor: pointer;
  background-color: #7877a2;
}

@media all and (min-width: 840px) {
  .btn-get-activity {
    font-size: 2rem;
    padding: 1rem;
    max-width: 520px;
  }
}

.main-content--activity-page {
    margin-bottom: 2rem;
}

.header__activity{
    text-align: center;
    margin-bottom: 1rem;
    font-size: 1.3rem;
}

.activity-card, 
.loading-card,
.error-card {
    margin: 0.5rem 1rem;
    background-color: white;
    text-align: center;
    padding: 1.5rem 1rem;
    border-radius: 2rem;
}

.activity-card__header {
    padding: 0.5rem;
    background-color:  #8e8cc2;
    color: white;
    font-size: 0.7rem;
}

.card__info__type {
    padding-top: 0.3rem;
    font-style: italic;
    font-size: 0.8rem;
}

.input-container {
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .card__info--circle { 
    line-height: 80px;
    width: 80px; 
    height: 80px; 
    border-radius: 50%; 
    background-color: rgb(172, 204, 238);
    text-align: center;
    font-size: 1.5rem;
    margin: 1rem auto;
  } 
  .no_participants {
    border-radius: 0.2rem;
    font-family: 'Work Sans', sans-serif;
    border:none;
    background-color: rgb(172, 204, 238);
    padding: 0.3rem;
    margin-left: 0.5rem;
    text-align: center;
}
.form {
    margin-bottom: 1rem;
}

@media all and (min-width: 840px) {
    .header__activity{ 
        font-size: 3rem;
    }

    .activity-card, 
    .error-card { 
        max-width: 1000px;
        margin: 2rem auto;
    }

    .activity-card__header {
        font-size: 1.5rem;
    }

    .activity-card__info {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-align-items: center;
                align-items: center;
    }

    .card__info__type {
        font-size: 1.2rem;
    }

    .card__info--circle {  
        line-height: 200px;
        width: 200px; 
        height: 200px; 
        font-size: 3rem;
    }

    .card__info__text{
        font-size: 1.2rem;
    }

    .no_participants, 
    .label__participants { 
        margin-top: 1.2rem;
        font-size: 1.5rem;
    }
}

.main-container--home-page {
    text-align: center;
}

.header__welcome {
    margin: 4rem 0;
    font-size: 2rem;
    color: black;
    text-shadow: -0.2px 0 white, 0 0.2px white, 0.2px 0 white, 0 -0.2px white;
}

@media all and (min-width:840px) {
    .header__welcome {
        font-size: 5rem;
        margin: 8rem 0;
    }
}
